<template>
    <input  :value="value" @input="input" type="text"  />
</template>

<script>
export default {
  inheritAttrs: false,
  props: ['value'],
  data: function () {
    return {}
  },
  methods: {
    input (event) {
      this.$emit('input', event.target.value)
    }
  }
}
</script>

<style scoped lang="scss">
@import '../styles/_vars.scss';
@import '../styles/_mixins.scss';
input {
  @include base-input-box;
  padding-left: 50px;
  font-size: 24px;
  line-height: 28px;
  font-family: $base-font-family;
  background: url(../assets/search.svg) no-repeat scroll 19px 15px;
  width: 100%;
}
</style>



