import _ from 'lodash'
export class UsersSearch {
  constructor () {
    this.find = _.memoize(this._find)
  }
  init (allUsers) {
    this.allUsers = Object.freeze(allUsers.map(u => Object.freeze(u)))
    this.preparedList = allUsers.map(u => {
      return {
        title: u.title.toLowerCase(),
        name: u.name.toLowerCase(),
        city: u.city.toLowerCase(),
        address: u.address.toLowerCase(),
        email: u.email.toLowerCase(),
        original: u
      }
    })
  }
  _find (searchText) {
    if (!searchText) {
      return this.allUsers
    }
    const st = searchText.toLowerCase()
    return Object.freeze(this.preparedList.filter(u => {
      return u.title.toLowerCase().indexOf(st) >= 0 ||
              u.email.toLowerCase().indexOf(st) >= 0 ||
              u.name.toLowerCase().indexOf(st) >= 0 ||
              u.city.toLowerCase().indexOf(st) >= 0 ||
              u.address.toLowerCase().indexOf(st) >= 0
    }).map(u => u.original))
  }
}



