import Vue from 'vue'
import Router from 'vue-router'
import UsersList from '../components/users-list'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/users/search/'
    },
    {
      path: '/users/search/:text',
      name: 'users-list',
      component: UsersList
    },
    {
      path: '/users/search/',
      name: 'users-list',
      component: UsersList
    }
  ]
})



