<template>
<div class="layout-container">
  <div class="users-list">
    <search-box class="search-box" v-model="searchText" />
    <div v-if="list && list.length > 0" class="total-hint">{{list.length}}</div>
    <template v-if="loaded">
      <!-- TODO standart list behavier, keyboard navigation, a11y -->
      <recycle-scroller
          class="scroller"
          :items="list"
          :item-size="147"
          key-field="id"
          v-slot="{ item }"
      >
        <users-list-item :item="item" :checked="checked[item.id]" @checked="onCheck" :searchText="searchText" ></users-list-item>
      </recycle-scroller>
    </template>
    <div v-else class="loading">Loading ...</div>
  </div>
</div>
</template>
<script>
import { RecycleScroller } from 'vue-virtual-scroller'
import 'vue-virtual-scroller/dist/vue-virtual-scroller.css'
import SearchBox from './search-box'
import UsersListItem from './users-list-item'
import {UsersSearch} from '../utils/users-search'
// import _ from 'lodash'
export default {
  name: 'users-list',
  data: function () {
    return {
      searchText: '',
      inputText: '',
      list: [],
      loaded: false,
      allItems: undefined,
      checked: []
    }
  },
  created: function () {
    this.searchText = this.$route.params.text || ''
    this.inputText = this.searchText
    fetch('static/users.json').then(r => {
      return r.json()
    }).then(data => {
      data = data.map((u, idx) => {
        u.id = idx
        return u
      })
      this.allItems = data// .slice(0,1000)
      this.us = new UsersSearch()
      this.us.init(this.allItems)
      this.filterList()
      this.loaded = true
    })
  },
  components: {
    'recycle-scroller': RecycleScroller,
    'search-box': SearchBox,
    'users-list-item': UsersListItem
  },
  watch: {
    searchText: function (v) {
      if (this.$route.params.text !== v) {
        this.$router.push({name: 'users-list', params: {text: this.searchText}})
      }
      this.updateList()
    },
    '$route' (to, from) {
      this.searchText = to.params.text || ''
    }
  },
  methods: {
    onSearchTextChange: function (e) {
      this.searchText = e.target.value
      this.$router.push({name: 'users-list', params: {text: this.searchText}})
      this.updateList()
    },
    onCheck: function (id) {
      this.$set(this.checked, id, !this.checked[id])
    },
    filterList: function () {
      if (!this.allItems) {
        return
      }
      this.list = this.us.find(this.searchText)
    }
  },
  computed: {
    updateList: function () {
      return this.filterList
      // return _.debounce(this.filterList, 100)
    }
  }

}
</script>

<style scoped lang="scss" >
    @import '../styles/_vars.scss';
    @import '../styles/_mixins.scss';
    .layout-container {
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        align-items: center;
    }
    .users-list {
        width: 50%;
        margin-top: 54px;
        background-color: #FFFFFF;
        padding-top: 19px;
        padding-left: 12px;
        padding-right: 12px;
        box-sizing: border-box;
        width: 564px;
        position: relative;
        .total-hint {
          position: absolute;
          top: 36px;
          right: 30px;
          z-index: 1000;
          opacity: 0.05;
        }
    }
    .search-box {
      width: 100%;
      margin-bottom: 30px;
    }
    $scroller-height: 147px * 3;
    .scroller {
        height: $scroller-height;
        overflow-y: scroll;
        padding-right: 12px;
        @include base-scroller;
    }
    .loading {
      min-height: $scroller-height;
    }
</style>



