<template>
  <div class="card flex-row" >
    <div class="avatar">
      <img :src="item.avatar" :key="item.id">
    </div>
    <div class="flex-column flex description-wrapper">
      <div class="description flex-column flex">
        <div class="flex-row" >
          <div class="name" ><text-highlight :highlightStyle="markStyle"  :queries="searchText">{{item.name}}</text-highlight></div>
          <div class="flex"></div>
          <div class="email"><text-highlight :highlightStyle="markStyle" :queries="searchText">{{item.email}}</text-highlight></div>
        </div>
        <div class="title"><text-highlight :highlightStyle="markStyle" :queries="searchText">{{item.title}}</text-highlight></div>
        <div class="address"><text-highlight :highlightStyle="markStyle" :queries="searchText">{{item.address}}</text-highlight>, <text-highlight :highlightStyle="markStyle" :queries="searchText">{{item.city}}</text-highlight></div>
      </div>
      <div class="actions">
        <button class="check-button"  @click="onCheck"><span v-if="checked">Skip Selection</span><span v-else>Mark As Suitable</span></button>
      </div>
    </div>
  </div>
</template>

<script>
import TextHighlight from 'vue-text-highlight'
export default {
  data: function () {
    return {
      markStyle: Object.freeze({backgroundColor: '#fff73b'})
    }
  },
  props: ['item', 'checked', 'searchText'],
  methods: {
    onCheck: function () {
      this.$emit('checked', this.item.id)
    }
  },
  components: {
    'text-highlight': TextHighlight
  }
}
</script>

<style scoped lang="scss">
    @import '../styles/_vars.scss';
    @import '../styles/_mixins.scss';
    $card-border-radius: 3px;
    .card {
        height: 136px;
        box-sizing: border-box;
        background: #FAFAFA;
        box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.24), 0px 0px 2px rgba(0, 0, 0, 0.12);
        border-radius: $card-border-radius;
        margin-bottom: 21px;
        .avatar {
          background-color: #bbbbbb;
          border-top-left-radius: $card-border-radius;
          border-bottom-left-radius: $card-border-radius;
          img {
            width: 134px;
            height: 134px;
          }
        }
        .description-wrapper {
          overflow: hidden;
        }
        .description {
          padding-top: 10px;
          padding-left: 27px;
          padding-right: 10px;
          overflow: hidden;
          border-bottom: 1px solid rgba(0, 0, 0, 0.12);
          div {
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            text-align: left;
          }
          .name {
            font-size: 24px;
            line-height: 32px;
            color: rgba(0, 0, 0, 0.87);
          }
          .email {
            font-size: 14px;
            line-height: 16px;
            color: rgba(0, 0, 0, 0.54);
            margin-left: 5px;
          }
          .title {
            font-weight: bold;
            font-size: 14px;
            line-height: 20px;
            color: rgba(0, 0, 0, 0.543846)
          }
          .address {
            font-size: 14px;
            line-height: 20px;
            color: rgba(0, 0, 0, 0.543846);
          }
        }
        .actions {
          padding-left: 32px;
          text-align: left;
        }
    }
    .check-button {
      @include base-button;
      min-width: 138.62px;
    }
</style>



